.section.about {
  padding: 20px;
  text-align: center;
  background-color: #f4f4f4;
  margin: 50px auto;
  max-width: 1000px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.about-image img {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 50%;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.about-text {
  flex: 1;
  margin-top: 20px;
  font-size: 1.2em;
  color: #333;
  text-align: justify;
  line-height: 1.8;
  max-width: 800px;
}

@media (min-width: 768px) {
  .about-content {
      flex-direction: row;
      align-items: flex-start;
      text-align: left;
  }

  .about-image {
      flex-shrink: 0;
      margin-right: 20px;
  }

  .about-text {
      margin-top: 0;
  }
}